.modal {
  background-color: rgba(0, 0, 0, 0.6);
  cursor: zoom-out;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
  padding: 40px 0;

  @media (min-width: 768px) {
    padding: 32px 70px;
  }
}

.close_button {
  color: var(--white);
  border: none;
  background: none;
  position: fixed;
  top: 8px;
  left: 8px;
  padding: 0;
  width: 24px;
  height: 24px;
  fill: currentColor;

  svg {
    width: 24px;
    height: 24px;
  }
}
